import {
  Button,
  Form,
  Input,
  Typography,
  Divider,
  Tag,
  Select,
  Col,
  Row,
  message as Msg
} from 'antd';
import React, { useEffect, useRef, useState } from 'react';
import type { InputRef } from 'antd';
import axios, { AxiosResponse } from 'axios';
import dayjs from 'dayjs';
import { PlusOutlined } from '@ant-design/icons';
import QS from 'qs';
import QRCode from 'qrcode';

import styles from './App.module.scss';

const { Title } = Typography;

const Tags = ({
  value = '',
  addBtnText,
  onChange
}: {
  value?: string;
  addBtnText?: string;
  onChange?: (data: string) => void;
}) => {
  const [inputVisible, setInputVisible] = useState(false);
  const inputRef = useRef<InputRef>(null);

  const arr = value.split(',').filter((item) => item);

  const handleInputConfirm = (e: any) => {
    setInputVisible(false);

    const val = e.target.value;

    if (typeof onChange === 'function' && val) {
      onChange(Array.from(new Set([...arr, e.target.value])).join(','));
    }
  };

  const showInput = () => {
    setInputVisible(true);
  };

  return (
    <>
      {arr?.map((tag) => {
        return (
          <Tag
            key={tag}
            closable
            onClose={() => {
              const newTags: string[] = arr.filter(t => t !== tag);
              if (typeof onChange === 'function') {
                onChange(newTags.join(','));
              }
            }}>
            {tag}
          </Tag>
        );
      })}
      {inputVisible && (
        <Input
          size='small'
          className={styles['tag-input']}
          ref={inputRef}
          placeholder='UPUID'
          type='text'
          onBlur={handleInputConfirm}
          onPressEnter={handleInputConfirm}
        />
      )}
      {!inputVisible && (
        <Tag onClick={showInput}>
          <PlusOutlined /> {addBtnText}
        </Tag>
      )}
    </>
  );
};

interface ResType<T> {
  statusCode: number;
  message: string;
  data: T;
}

interface ConfigData {
  isLogin: boolean;
  vipStatus: boolean;
  config: Config;
}

interface Config {
  id: number;
  duration: number;
  expire: number;
  exclude: string;
  include: string;
  keywords: string;
  fileName: string;
  saveType: string;
  ftpAccount: string;
  ftpPassword: string;
  ftpRemote: string;
  outputPath: string;
  lastTime: number;
}

const getQrCode = async () => {
  return axios.get('/api/v1/qrcode');
};

const getConfig = async () => {
  return axios.get<any, AxiosResponse<ResType<ConfigData>>>('/api/v1/config');
};

const poll = async (params: any) => {
  return axios.get<any, AxiosResponse<ResType<number>>>('/api/v1/poll', { params });
};

const edit = async (data: any) => {
  return axios<any, AxiosResponse<ResType<boolean>>>({
    url: '/api/v1/edit',
    method: 'post',
    data: QS.parse(data)
  });
};

function polling(key: string) {
  setTimeout(async () => {
    const {
      data: { statusCode, data, message }
    } = await poll({ key });

    if (statusCode !== 200) {
      return Msg.error(message);
    }

    if (data === 0) {
      window.location.reload();
    } else if (data === 86038) {
      Msg.error('');
    } else {
      polling(key);
    }
  }, 1500);
}

const App: React.FC = React.memo(() => {
  const [form] = Form.useForm();
  const [cfg, setConfig] = useState<ConfigData>({
    isLogin: true,
    vipStatus: false,
    config: {
      id: 1,
      exclude: '',
      outputPath: '',
      include: '',
      keywords: '',
      ftpRemote: '',
      ftpPassword: '',
      ftpAccount: '',
      fileName: '{{title}}',
      saveType: 'local',
      duration: 300,
      expire: 600,
      lastTime: 0
    }
  });

  const saveType = cfg.config.saveType;

  const onFinish = async (values: any) => {
    console.log(values);
    const {
      data: { statusCode, data, message }
    } = await edit({
      ...values,
      id: cfg.config.id
    });

    if (!values.outputPath) {
      return Msg.warning(saveType === 'ftp' ? 'FTP/' : 'Windows/Linux');
    }

    if (statusCode === 200 && data) {
      Msg.success('');
    } else {
      Msg.error(message || '');
    }
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log('Failed:', errorInfo);
  };

  useEffect(() => {
    async function init() {
      const {
        data: { statusCode, data, message }
      } = await getConfig();

      if (statusCode !== 200) {
        return Msg.error(message);
      }

      setConfig(data);
      form.setFieldsValue(data.config);
    }

    init();
  }, [form]);

  useEffect(() => {
    async function init() {
      const qr = await getQrCode();
      const { qrcode_key, url } = qr.data.data;
      QRCode.toCanvas(document.getElementById('canvas'), url, function (error) {
        if (error) console.error(error);
        console.log('success!');
      });

      polling(qrcode_key);
    }

    if (!cfg.isLogin) {
      init();
    }
  }, [cfg.isLogin]);

  return (
    <div className={styles.app}>
      <div className={styles.hd}>
        <Title></Title>
      </div>
      <Row>
        <Col span={8}>{cfg.isLogin ? '' : ''}</Col>
        <Col span={8}>{cfg.vipStatus ? '' : ''}</Col>
        <Col span={8}>{dayjs(cfg.config.lastTime * 1000).format('YYYY-MM-DD HH:mm')}</Col>
      </Row>
      <Divider orientation='left'></Divider>
      {cfg.isLogin ? (
        <Form
          form={form}
          name='basic'
          labelCol={{ span: 8 }}
          onValuesChange={({ saveType }) => {
            if (saveType) {
              form.setFieldValue('outputPath', '');
              setConfig((ct) => {
                return {
                  ...ct,
                  config: {
                    ...ct.config,
                    saveType
                  }
                };
              });
            }
          }}
          wrapperCol={{ span: 16 }}
          initialValues={cfg.config}
          onFinish={onFinish}
          onFinishFailed={onFinishFailed}
          autoComplete='off'>
          <Form.Item label='' name='duration' extra="">
            <Input addonAfter="" type='number' placeholder='' />
          </Form.Item>

          <Form.Item label='' name='expire' extra="">
            <Input addonAfter="" type='number' placeholder='' />
          </Form.Item>

          <Form.Item
            label=''
            name='fileName'
            tooltip='{{title}}{{bvid}}{{cid}}'>
            <Input placeholder='' />
          </Form.Item>

          <Form.Item label='' name='saveType'>
            <Select
              options={[
                {
                  value: 'local',
                  label: ''
                },
                {
                  value: 'ftp',
                  label: 'FTP'
                }
              ]}
            />
          </Form.Item>

          {cfg.config.saveType === 'ftp' ? (
            <>
              <Form.Item label='FTP ' name='ftpRemote'>
                <Input type='text' placeholder='FTP' />
              </Form.Item>

              <Form.Item label='FTP ' name='ftpAccount'>
                <Input type='text' placeholder='FTP' />
              </Form.Item>

              <Form.Item label='FTP ' name='ftpPassword'>
                <Input type='text' placeholder='FTP' />
              </Form.Item>
            </>
          ) : null}

          <Form.Item
            label={saveType === 'ftp' ? 'FTP' : ''}
            name='outputPath'
            tooltip='output/FTPFTP'>
            <Input placeholder='WindowsLinux' />
          </Form.Item>

          <Form.Item label='uid' name='exclude' tooltip='UP'>
            <Tags addBtnText='UPUID' />
          </Form.Item>

          <Form.Item
            label='uid'
            name='include'
            tooltip='UP'>
            <Tags addBtnText='UPUID' />
          </Form.Item>

          <Form.Item label='' name='keywords' tooltip=''>
            <Tags addBtnText='' />
          </Form.Item>

          <Form.Item wrapperCol={{ offset: 8, span: 16 }}>
            <Button type='primary' htmlType='submit'>
              
            </Button>
          </Form.Item>
        </Form>
      ) : (
        <div className={styles.qrcode}>
          <div className={styles.img}>
            <canvas id='canvas'></canvas>
          </div>
          <div className={styles.tips}></div>
        </div>
      )}
    </div>
  );
});

export default App;
